"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/app/auth-context"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { ArrowLeft, Wrench, Building2, Package, Star } from "lucide-react"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

interface Feedback {
  form_id: number
  form_name: string
  form_status: string
  form_description: string
  is_active: boolean
  created_at: string
}

interface Ticket {
  Status: number
  created_at: string
}

interface Asset {
  site: string
  category: string
}

export default function ReportPage() {
  const router = useRouter()
  const { user, logout, isLoading } = useAuth()
  const [feedbacks, setFeedbacks] = useState<Feedback[]>([])
  const [tickets, setTickets] = useState<Ticket[]>([])
  const [assets, setAssets] = useState<Asset[]>([])
  const [loading, setLoading] = useState(true)
  
  const [feedbackStats, setFeedbackStats] = useState({
    total: 0,
    excellent: 0,
    good: 0,
    fair: 0,
    poor: 0,
    bad: 0,
    avgRating: 0
  })

  const [ticketStats, setTicketStats] = useState({
    total: 0,
    pending: 0,
    inProgress: 0,
    completed: 0,
    cancelled: 0
  })

  const [siteStats, setSiteStats] = useState<{[key: string]: number}>({})
  const [categoryStats, setCategoryStats] = useState<{[key: string]: number}>({})

  const handleLogout = async () => {
    await logout()
    router.push('/')
  }

  useEffect(() => {
    if (isLoading) return // รอให้โหลดเสร็จก่อน
    
    if (!user) {
      router.push('/')
      return
    }
    if (user.role !== 'admin') {
      router.push('/dashboard')
      return
    }
  }, [user, router, isLoading])

  useEffect(() => {
    fetchFeedbacks()
  }, [])

  const fetchFeedbacks = async () => {
    try {
      const response = await fetch('/api/feedback')
      if (response.ok) {
        const data = await response.json()
        setFeedbacks(data)
        
        // คำนวณสถิติ
        const total = data.length
        const ratings = data.map((f: Feedback) => parseInt(f.form_status))
        const excellent = ratings.filter((r: number) => r === 5).length
        const good = ratings.filter((r: number) => r === 4).length
        const fair = ratings.filter((r: number) => r === 3).length
        const poor = ratings.filter((r: number) => r === 2).length
        const bad = ratings.filter((r: number) => r === 1).length
        const avgRating = total > 0 ? (ratings.reduce((a: number, b: number) => a + b, 0) / total).toFixed(2) : 0
        
        setStats({ total, excellent, good, fair, poor, bad, avgRating: Number(avgRating) })
      }
    } catch (error) {
      console.error('Failed to fetch feedbacks:', error)
    } finally {
      setLoading(false)
    }
  }

  const getRatingLabel = (rating: string) => {
    const labels: { [key: string]: string } = {
      '5': 'พึงพอใจมากที่สุด',
      '4': 'พึงพอใจมาก',
      '3': 'พึงพอใจปานกลาง',
      '2': 'พึงพอใจน้อย',
      '1': 'ไม่พึงพอใจ'
    }
    return labels[rating] || '-'
  }

  const getRatingBadge = (rating: string) => {
    const styles: { [key: string]: string } = {
      '5': 'bg-green-100 text-green-800 border-green-300',
      '4': 'bg-blue-100 text-blue-800 border-blue-300',
      '3': 'bg-yellow-100 text-yellow-800 border-yellow-300',
      '2': 'bg-orange-100 text-orange-800 border-orange-300',
      '1': 'bg-red-100 text-red-800 border-red-300'
    }
    return styles[rating] || 'bg-gray-100 text-gray-800 border-gray-300'
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50">
        <header className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
                <ArrowLeft className="h-5 w-5" />
              </Button>
              <div>
                <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
                <p className="text-sm text-muted-foreground">รายงานการประเมิน</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              <div className="text-right">
                <p className="font-medium text-sm">{user?.name || user?.username}</p>
                <p className="text-xs text-muted-foreground capitalize">{user?.role}</p>
              </div>
              <Button variant="outline" onClick={handleLogout}>Logout</Button>
            </div>
          </div>
        </header>
        <div className="container mx-auto p-6">
          <div className="flex items-center justify-center h-[calc(100vh-200px)]">
            <div className="text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
              <p className="text-muted-foreground">กำลังโหลดข้อมูล...</p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <header className="bg-white border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center justify-between">
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon" onClick={() => router.push('/dashboard')}>
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <div>
              <h1 className="text-2xl font-bold">ระบบบำรุงรักษา</h1>
              <p className="text-sm text-muted-foreground">รายงานการประเมิน</p>
            </div>
          </div>
          <div className="flex items-center gap-4">
            <div className="text-right">
              <p className="font-medium text-sm">{user?.name || user?.username}</p>
              <p className="text-xs text-muted-foreground capitalize">{user?.role}</p>
            </div>
            <Button variant="outline" onClick={handleLogout}>Logout</Button>
          </div>
        </div>
      </header>

      <div className="container mx-auto p-6 space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold">รายงานผลการประเมินความพึงพอใจ</h1>
            <p className="text-muted-foreground mt-1">สรุปผลการประเมินจากผู้ใช้งาน</p>
          </div>
        </div>

      {/* สถิติภาพรวม */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">จำนวนการประเมินพึงหมด</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold">{stats.total}</div>
            <p className="text-xs text-muted-foreground mt-1">รายการ</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">คะแนนเฉลี่ย</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-blue-600">{stats.avgRating}</div>
            <p className="text-xs text-muted-foreground mt-1">จาก 5 คะแนน</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">พึงพอใจมากที่สุด</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-green-600">{stats.excellent}</div>
            <p className="text-xs text-muted-foreground mt-1">
              {stats.total > 0 ? ((stats.excellent / stats.total) * 100).toFixed(1) : 0}%
            </p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-3">
            <CardTitle className="text-sm font-medium text-muted-foreground">ไม่พึงพอใจ</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-3xl font-bold text-red-600">{stats.bad}</div>
            <p className="text-xs text-muted-foreground mt-1">
              {stats.total > 0 ? ((stats.bad / stats.total) * 100).toFixed(1) : 0}%
            </p>
          </CardContent>
        </Card>
      </div>

      {/* กราฟแท่งแสดงการกระจายของคะแนน */}
      <Card>
        <CardHeader>
          <CardTitle>การกระจายของคะแนนประเมิน</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {[
              { rating: 5, label: 'พึงพอใจมากที่สุด', count: stats.excellent, color: 'bg-green-500' },
              { rating: 4, label: 'พึงพอใจมาก', count: stats.good, color: 'bg-blue-500' },
              { rating: 3, label: 'พึงพอใจปานกลาง', count: stats.fair, color: 'bg-yellow-500' },
              { rating: 2, label: 'พึงพอใจน้อย', count: stats.poor, color: 'bg-orange-500' },
              { rating: 1, label: 'ไม่พึงพอใจ', count: stats.bad, color: 'bg-red-500' }
            ].map((item) => {
              const percentage = stats.total > 0 ? (item.count / stats.total) * 100 : 0
              return (
                <div key={item.rating} className="space-y-2">
                  <div className="flex items-center justify-between text-sm">
                    <div className="flex items-center gap-2">
                      <span className="font-medium">{item.label}</span>
                      <span className="text-muted-foreground">({item.count})</span>
                    </div>
                    <span className="font-medium">{percentage.toFixed(1)}%</span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-3 overflow-hidden">
                    <div
                      className={`h-full ${item.color} transition-all duration-500`}
                      style={{ width: `${percentage}%` }}
                    />
                  </div>
                </div>
              )
            })}
          </div>
        </CardContent>
      </Card>

      {/* ตารางรายละเอียดการประเมิน */}
      <Card>
        <CardHeader>
          <CardTitle>รายละเอียดการประเมินพึงหมด</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="rounded-md border">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead className="w-[100px]">ลำดับ</TableHead>
                  <TableHead>เลขที่คำขอ</TableHead>
                  <TableHead>ระดับความพึงพอใจ</TableHead>
                  <TableHead>คะแนน</TableHead>
                  <TableHead>หมายเหตุ</TableHead>
                  <TableHead className="text-right">วันที่ประเมิน</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {feedbacks.length === 0 ? (
                  <TableRow>
                    <TableCell colSpan={6} className="text-center text-muted-foreground py-8">
                      ยังไม่มีข้อมูลการประเมิน
                    </TableCell>
                  </TableRow>
                ) : (
                  feedbacks.map((feedback, index) => (
                    <TableRow key={feedback.form_id}>
                      <TableCell className="font-medium">{index + 1}</TableCell>
                      <TableCell className="font-mono">{feedback.form_name}</TableCell>
                      <TableCell>
                        <Badge className={getRatingBadge(feedback.form_status)}>
                          {getRatingLabel(feedback.form_status)}
                        </Badge>
                      </TableCell>
                      <TableCell>
                        <div className="flex gap-0.5">
                          {[1, 2, 3, 4, 5].map((star) => (
                            <span
                              key={star}
                              className={`text-lg ${
                                parseInt(feedback.form_status) >= star
                                  ? 'text-yellow-400'
                                  : 'text-gray-300'
                              }`}
                            >
                              ★
                            </span>
                          ))}
                        </div>
                      </TableCell>
                      <TableCell className="max-w-xs truncate">
                        {feedback.form_description || '-'}
                      </TableCell>
                      <TableCell className="text-right text-sm">
                        {new Date(feedback.created_at).toLocaleDateString('th-TH', {
                          year: 'numeric',
                          month: 'short',
                          day: 'numeric',
                          hour: '2-digit',
                          minute: '2-digit'
                        })}
                      </TableCell>
                    </TableRow>
                  ))
                )}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
      </div>
    </div>
  )
}
